*,
:before,
:after {
  box-sizing: border-box; }

/*
 * 1. prevent chrome from hiding background images and colors by default when printing
 */
body {
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  line-height: 1.7;
  font-size: 16px;
  color: #202020;
  background-color: #fff;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* stylelint-disable-next-line property-no-vendor-prefix */
  -webkit-print-color-adjust: exact;
  /* 1 */ }
  @media print {
    body {
      padding-top: 0 !important; } }

button {
  padding: 0;
  background: 0;
  border: 0;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer; }

input,
textarea {
  border-radius: 0; }

textarea {
  resize: vertical; }

figure {
  margin: 0; }

hr {
  background-color: #dedede;
  margin-top: 2rem;
  margin-bottom: 2rem;
  height: 1px;
  border: 0; }

blockquote {
  margin: 0; }
  blockquote p:last-child {
    margin-bottom: 0; }

dl,
dt,
dd {
  margin: 0; }

a {
  color: #0d395f;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

[href^='mailto:'] {
  word-wrap: break-word; }

strong {
  font-weight: 600; }

fieldset {
  margin: 0;
  padding: 0;
  border: 0; }

p {
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  font-size: 1.125rem;
  line-height: 1.7;
  margin-top: 0;
  margin-bottom: 0; }

address {
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  font-style: normal;
  font-size: 1.125rem;
  line-height: 1.7; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

ol,
ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif;
  font-weight: 400;
  line-height: 1.4;
  margin-top: 0;
  margin-bottom: 0; }

.h1,
h1 {
  font-size: 2.25rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }
  @media only screen and (min-width: 768px) {
    .h1,
    h1 {
      font-size: 3rem; } }
  @media only screen and (min-width: 1024px) {
    .h1,
    h1 {
      font-size: 4rem; } }

.h2,
h2 {
  font-size: 1.5rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }
  @media only screen and (min-width: 1024px) {
    .h2,
    h2 {
      font-size: 1.75rem; } }

.h3,
h3 {
  font-size: 1.25rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }

.h4,
h4 {
  font-size: 1.125rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }

.h5,
h5 {
  font-size: 1rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }

.h6,
h6 {
  font-size: 0.875rem;
  line-height: 1.4;
  font-family: "sentinel ssm a", "sentinel ssm b", georgia, serif; }

svg {
  fill: currentColor; }

wbr:after {
  content: '\00200B'; }

@media (prefers-reduced-motion: reduce) {
  *,
  ::before,
  ::after {
    animation-delay: -1ms !important;
    animation-duration: 1ms !important;
    animation-iteration-count: 1 !important;
    background-attachment: initial !important;
    scroll-behavior: auto !important;
    transition-delay: 0s !important;
    transition-duration: 0s !important; } }

.form {
  padding: 2px;
  /* push in so elems arent hidden */ }

.form-field {
  margin-bottom: 2rem; }

.field-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  margin-bottom: 0.5rem; }

.description {
  font-size: 0.875rem;
  margin-bottom: 0.25rem; }

select,
textarea,
input[type='text'] {
  display: block;
  width: 100%;
  border: 1px solid #202020;
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  font-size: 1rem;
  line-height: 1;
  background-color: #fff;
  color: #202020;
  padding: 1rem 1rem;
  border-radius: 0;
  appearance: none;
  padding: 0.5rem; }
  select .is-error,
  textarea .is-error,
  input[type='text'] .is-error {
    border-color: #dc3545; }
  select .is-valid,
  textarea .is-valid,
  input[type='text'] .is-valid {
    border-color: #5e7d31; }

select:not([multiple]) {
  background-color: #fff;
  background-image: url("data:image/png;base64,");
  background-position: right 1rem center;
  background-repeat: no-repeat;
  padding-right: 3rem; }
  select:not([multiple])::-ms-expand {
    display: none; }

textarea {
  min-height: 200px;
  resize: vertical;
  line-height: 1.7; }

input[type='submit'] {
  display: inline-block;
  font-family: "whitney ssm a", "whitney ssm b", tahoma, geneva, sans-serif;
  text-align: center;
  border: 1px solid transparent;
  cursor: pointer;
  background: transparent;
  color: #0d395f;
  transition: color 0.2s, background-color 0.2s, border-color 0.2s;
  padding: 1rem 2rem;
  font-size: 1rem;
  line-height: 1;
  border-radius: 0;
  white-space: nowrap;
  background-color: #0d395f;
  color: #fff;
  border-color: #0d395f; }
  input[type='submit']:hover, input[type='submit']:focus {
    text-decoration: none; }
  input[type='submit']:hover, input[type='submit']:focus {
    background-color: #13548c;
    color: #fff;
    border-color: #13548c; }

.pd-radio .value > span,
.pd-checkbox .value > span {
  display: block;
  display: flex;
  margin-bottom: 0.25rem; }

input[type='radio'],
input[type='checkbox'] {
  position: relative;
  top: 3px; }

input[type='radio'] + label,
input[type='checkbox'] + label {
  font-size: 0.875rem;
  font-weight: 400;
  padding-left: 0.5rem;
  margin-bottom: 0;
  cursor: pointer; }

/* error states */
.form-field.error textarea,
.form-field.error select,
.form-field.error input[type='text'] {
  border-color: #dc3545; }

p.error {
  margin-top: 0.5rem;
  margin-bottom: 0;
  font-size: 14px;
  color: #dc3545; }

/* required asterisk */
.form-field.required .field-label:after {
  content: '*';
  color: #dc3545;
  font-size: 0.875rem; }
